import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Settings, Save, Info, RefreshCw, Search, User, ImageIcon } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { usePointsSettings, useUpdatePointsSetting } from "@/hooks/usePoints";
import { toast } from "sonner";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Switch } from "@/components/ui/switch";
import { supabase } from "@/integrations/supabase/client";

const AdminPointsSettings = () => {
  const { t } = useTranslation();
  const { data: settings, isLoading } = usePointsSettings();
  const updateSetting = useUpdatePointsSetting();
  
  const [commentPoints, setCommentPoints] = useState<string>("");
  const [minCommentLength, setMinCommentLength] = useState<string>("");
  const [postPoints, setPostPoints] = useState<string>("");
  const [minPostLength, setMinPostLength] = useState<string>("");
  const [postRequireMedia, setPostRequireMedia] = useState<string>("");
  const [hasChanges, setHasChanges] = useState(false);
  const [resetUserInput, setResetUserInput] = useState("");
  const [isResetting, setIsResetting] = useState(false);
  const [isResettingPost, setIsResettingPost] = useState(false);
  const [isSearching, setIsSearching] = useState(false);
  const [foundUser, setFoundUser] = useState<{ id: string; email: string; display_name: string } | null>(null);

  // Initialize values when settings load
  useState(() => {
    if (settings) {
      const commentPointsSetting = settings.find(s => s.key === "comment_points");
      const minLengthSetting = settings.find(s => s.key === "min_comment_length");
      const postPointsSetting = settings.find(s => s.key === "post_points");
      const minPostLengthSetting = settings.find(s => s.key === "min_post_length");
      const postRequireMediaSetting = settings.find(s => s.key === "post_require_media");
      if (commentPointsSetting) setCommentPoints(commentPointsSetting.value);
      if (minLengthSetting) setMinCommentLength(minLengthSetting.value);
      if (postPointsSetting) setPostPoints(postPointsSetting.value);
      if (minPostLengthSetting) setMinPostLength(minPostLengthSetting.value);
      if (postRequireMediaSetting) setPostRequireMedia(postRequireMediaSetting.value);
    }
  });

  // Update state when settings change
  if (settings && !commentPoints && !minCommentLength && !postPoints && !minPostLength && !postRequireMedia) {
    const commentPointsSetting = settings.find(s => s.key === "comment_points");
    const minLengthSetting = settings.find(s => s.key === "min_comment_length");
    const postPointsSetting = settings.find(s => s.key === "post_points");
    const minPostLengthSetting = settings.find(s => s.key === "min_post_length");
    const postRequireMediaSetting = settings.find(s => s.key === "post_require_media");
    if (commentPointsSetting && !commentPoints) setCommentPoints(commentPointsSetting.value);
    if (minLengthSetting && !minCommentLength) setMinCommentLength(minLengthSetting.value);
    if (postPointsSetting && !postPoints) setPostPoints(postPointsSetting.value);
    if (minPostLengthSetting && !minPostLength) setMinPostLength(minPostLengthSetting.value);
    if (postRequireMediaSetting && !postRequireMedia) setPostRequireMedia(postRequireMediaSetting.value);
  }

  const handleSave = async () => {
    try {
      await Promise.all([
        updateSetting.mutateAsync({ key: "comment_points", value: commentPoints }),
        updateSetting.mutateAsync({ key: "min_comment_length", value: minCommentLength }),
        updateSetting.mutateAsync({ key: "post_points", value: postPoints }),
        updateSetting.mutateAsync({ key: "min_post_length", value: minPostLength }),
        updateSetting.mutateAsync({ key: "post_require_media", value: postRequireMedia }),
      ]);
      toast.success(t("admin.pointsSettings.saveSuccess"));
      setHasChanges(false);
    } catch (error) {
      toast.error(t("admin.pointsSettings.saveFailed"));
    }
  };

  const handleValueChange = (setter: (value: string) => void, value: string) => {
    setter(value);
    setHasChanges(true);
  };

  const handleSearchUser = async () => {
    if (!resetUserInput.trim()) {
      toast.error(t("admin.pointsSettings.enterEmailOrId"));
      return;
    }

    setIsSearching(true);
    setFoundUser(null);
    
    try {
      const input = resetUserInput.trim();
      const isEmail = input.includes('@');
      
      if (isEmail) {
        // Search by email using edge function
        const { data, error } = await supabase.functions.invoke('get-user-emails');
        
        if (error) throw error;
        
        // Find user by email in the returned emails map
        const emailsMap = data?.emails || {};
        const userId = Object.keys(emailsMap).find(id => emailsMap[id]?.toLowerCase() === input.toLowerCase());
        
        if (userId) {
          // Get profile info
          const { data: profile } = await supabase
            .from('profiles')
            .select('display_name')
            .eq('id', userId)
            .single();
          
          setFoundUser({
            id: userId,
            email: emailsMap[userId],
            display_name: profile?.display_name || t("common.unknownUser", "Unknown User")
          });
          toast.success(t("admin.pointsSettings.userFound"));
        } else {
          toast.error(t("admin.pointsSettings.userNotFound"));
        }
      } else {
        // Assume it's a user ID, verify it exists
        const { data: profile, error } = await supabase
          .from('profiles')
          .select('id, display_name')
          .eq('id', input)
          .single();
        
        if (error || !profile) {
          toast.error(t("admin.pointsSettings.userNotFound"));
          return;
        }
        
        setFoundUser({
          id: profile.id,
          email: t("admin.pointsSettings.foundById", "(Found by ID)"),
          display_name: profile.display_name || t("common.unknownUser", "Unknown User")
        });
        toast.success(t("admin.pointsSettings.userFound"));
      }
    } catch (error) {
      console.error('Search error:', error);
      toast.error(t("admin.pointsSettings.searchFailed"));
    } finally {
      setIsSearching(false);
    }
  };

  const handleResetDailyComment = async () => {
    if (!foundUser) {
      toast.error(t("admin.pointsSettings.searchUserFirst"));
      return;
    }

    setIsResetting(true);
    try {
      // Delete today's comment transaction for the user
      const today = new Date().toISOString().split('T')[0];
      const { error, count } = await supabase
        .from('points_transactions')
        .delete()
        .eq('user_id', foundUser.id)
        .eq('type', 'comment')
        .gte('created_at', today)
        .lt('created_at', `${today}T23:59:59.999Z`);

      if (error) throw error;

      if (count === 0) {
        toast.info(t("admin.pointsSettings.noRecordFound"));
      } else {
        toast.success(t("admin.pointsSettings.resetSuccess"));
      }
      setResetUserInput("");
      setFoundUser(null);
    } catch (error) {
      console.error('Reset error:', error);
      toast.error(t("admin.pointsSettings.resetFailed"));
    } finally {
      setIsResetting(false);
    }
  };

  const handleResetDailyPost = async () => {
    if (!foundUser) {
      toast.error(t("admin.pointsSettings.searchUserFirst"));
      return;
    }

    setIsResettingPost(true);
    try {
      // Delete today's post transaction for the user
      const today = new Date().toISOString().split('T')[0];
      const { error, count } = await supabase
        .from('points_transactions')
        .delete()
        .eq('user_id', foundUser.id)
        .eq('type', 'post')
        .gte('created_at', today)
        .lt('created_at', `${today}T23:59:59.999Z`);

      if (error) throw error;

      if (count === 0) {
        toast.info(t("admin.pointsSettings.noPostRecordFound"));
      } else {
        toast.success(t("admin.pointsSettings.resetPostSuccess"));
      }
      setResetUserInput("");
      setFoundUser(null);
    } catch (error) {
      console.error('Reset post error:', error);
      toast.error(t("admin.pointsSettings.resetFailed"));
    } finally {
      setIsResettingPost(false);
    }
  };

  if (isLoading) {
    return <div className="p-6">{t("common.loading")}</div>;
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold">{t("admin.pointsSettings.title")}</h1>
          <p className="text-muted-foreground">{t("admin.pointsSettings.description")}</p>
        </div>
        <Button onClick={handleSave} disabled={!hasChanges || updateSetting.isPending}>
          <Save className="h-4 w-4 mr-2" />
          {t("common.save")}
        </Button>
      </div>

      <Alert>
        <Info className="h-4 w-4" />
        <AlertDescription>
          {t("admin.pointsSettings.info")}
        </AlertDescription>
      </Alert>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Settings className="h-5 w-5" />
            {t("admin.pointsSettings.commentSettings")}
          </CardTitle>
          <CardDescription>
            {t("admin.pointsSettings.commentSettingsDesc")}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid gap-4 md:grid-cols-2">
            <div className="space-y-2">
              <Label htmlFor="commentPoints">
                {t("admin.pointsSettings.commentPoints")}
              </Label>
              <Input
                id="commentPoints"
                type="number"
                min="0"
                value={commentPoints}
                onChange={(e) => handleValueChange(setCommentPoints, e.target.value)}
                placeholder="5"
              />
              <p className="text-sm text-muted-foreground">
                {t("admin.pointsSettings.commentPointsHelp")}
              </p>
            </div>

            <div className="space-y-2">
              <Label htmlFor="minCommentLength">
                {t("admin.pointsSettings.minCommentLength")}
              </Label>
              <Input
                id="minCommentLength"
                type="number"
                min="0"
                value={minCommentLength}
                onChange={(e) => handleValueChange(setMinCommentLength, e.target.value)}
                placeholder="10"
              />
              <p className="text-sm text-muted-foreground">
                {t("admin.pointsSettings.minCommentLengthHelp")}
              </p>
            </div>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Settings className="h-5 w-5" />
            {t("admin.pointsSettings.postSettings")}
          </CardTitle>
          <CardDescription>
            {t("admin.pointsSettings.postSettingsDesc")}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid gap-4 md:grid-cols-2">
            <div className="space-y-2">
              <Label htmlFor="postPoints">
                {t("admin.pointsSettings.postPoints")}
              </Label>
              <Input
                id="postPoints"
                type="number"
                min="0"
                value={postPoints}
                onChange={(e) => handleValueChange(setPostPoints, e.target.value)}
                placeholder="5"
              />
              <p className="text-sm text-muted-foreground">
                {t("admin.pointsSettings.postPointsHelp")}
              </p>
            </div>

            <div className="space-y-2">
              <Label htmlFor="minPostLength">
                {t("admin.pointsSettings.minPostLength")}
              </Label>
              <Input
                id="minPostLength"
                type="number"
                min="0"
                value={minPostLength}
                onChange={(e) => handleValueChange(setMinPostLength, e.target.value)}
                placeholder="10"
              />
              <p className="text-sm text-muted-foreground">
                {t("admin.pointsSettings.minPostLengthHelp")}
              </p>
            </div>
          </div>

          <div className="flex items-center justify-between rounded-lg border p-4">
            <div className="space-y-0.5">
              <div className="flex items-center gap-2">
                <ImageIcon className="h-4 w-4 text-muted-foreground" />
                <Label htmlFor="postRequireMedia" className="font-medium">
                  {t("admin.pointsSettings.postRequireMedia")}
                </Label>
              </div>
              <p className="text-sm text-muted-foreground">
                {t("admin.pointsSettings.postRequireMediaHelp")}
              </p>
            </div>
            <Switch
              id="postRequireMedia"
              checked={postRequireMedia === "true"}
              onCheckedChange={(checked) => 
                handleValueChange(setPostRequireMedia, checked ? "true" : "false")
              }
            />
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>{t("admin.pointsSettings.rulesPreview")}</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-2 text-sm">
            <p className="font-medium text-muted-foreground">{t("admin.pointsSettings.commentSettings")}</p>
            <p>
              • {t("admin.pointsSettings.rule1")} <strong>{minCommentLength || "10"}</strong> {t("admin.pointsSettings.characters")}
            </p>
            <p>
              • {t("admin.pointsSettings.rule2")} <strong>{commentPoints || "5"}</strong> {t("admin.pointsSettings.points")}
            </p>
            <p>
              • {t("admin.pointsSettings.rule3")}
            </p>
            
            <p className="font-medium text-muted-foreground pt-2">{t("admin.pointsSettings.postSettings")}</p>
            <p>
              • {t("admin.pointsSettings.postRule2")} <strong>{minPostLength || "10"}</strong> {t("admin.pointsSettings.characters")}
            </p>
            {postRequireMedia === "true" && (
              <p>
                • {t("admin.pointsSettings.postRule3")}
              </p>
            )}
            <p>
              • {t("admin.pointsSettings.postRule")} <strong>{postPoints || "5"}</strong> {t("admin.pointsSettings.points")}
            </p>
            <p>
              • {t("admin.pointsSettings.rule4")}
            </p>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <RefreshCw className="h-5 w-5" />
            {t("admin.pointsSettings.testTools")}
          </CardTitle>
          <CardDescription>
            {t("admin.pointsSettings.testToolsDesc")}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-3">
            <Label>
              {t("admin.pointsSettings.resetDailyComment")}
            </Label>
            <div className="flex gap-2">
              <Input
                value={resetUserInput}
                onChange={(e) => {
                  setResetUserInput(e.target.value);
                  setFoundUser(null);
                }}
                placeholder={t("admin.pointsSettings.emailOrIdPlaceholder")}
                className="flex-1"
              />
              <Button 
                onClick={handleSearchUser} 
                disabled={isSearching || !resetUserInput.trim()}
                variant="outline"
              >
                <Search className={`h-4 w-4 mr-2 ${isSearching ? 'animate-spin' : ''}`} />
                {t("admin.pointsSettings.search")}
              </Button>
            </div>
            
            {foundUser && (
              <div className="p-3 bg-muted rounded-lg space-y-3">
                <div className="flex items-center gap-2">
                  <User className="h-4 w-4 text-muted-foreground" />
                  <div>
                    <p className="font-medium">{foundUser.display_name}</p>
                    <p className="text-sm text-muted-foreground">{foundUser.email}</p>
                    <p className="text-xs text-muted-foreground font-mono">{foundUser.id}</p>
                  </div>
                </div>
                <div className="flex gap-2">
                  <Button 
                    onClick={handleResetDailyComment} 
                    disabled={isResetting}
                    variant="destructive"
                    size="sm"
                  >
                    <RefreshCw className={`h-4 w-4 mr-2 ${isResetting ? 'animate-spin' : ''}`} />
                    {t("admin.pointsSettings.reset")}
                  </Button>
                  <Button 
                    onClick={handleResetDailyPost} 
                    disabled={isResettingPost}
                    variant="destructive"
                    size="sm"
                  >
                    <RefreshCw className={`h-4 w-4 mr-2 ${isResettingPost ? 'animate-spin' : ''}`} />
                    {t("admin.pointsSettings.resetPost")}
                  </Button>
                </div>
              </div>
            )}
            
            <p className="text-sm text-muted-foreground">
              {t("admin.pointsSettings.resetHelp")}
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default AdminPointsSettings;
